/**
* WMOweatherCode
*
* 
* : https://www.jma.go.jp/bosai/forecast/img/100.svg,
      https://www.jma.go.jp/bosai/forecast/img/101.svg,
      https://www.jma.go.jp/bosai/forecast/img/200.svg,
      https://www.jma.go.jp/bosai/forecast/img/202.svg,
      https://www.jma.go.jp/bosai/forecast/img/300.svg,
      https://www.jma.go.jp/bosai/forecast/img/302.svg,
      https://www.jma.go.jp/bosai/forecast/img/400.svg
* @link https://www.jma.go.jp/jma/kishou/info/coment.html
*
* @param {number} weatherCode WMOweatherCode
* @returns {Object<label<String>, icon<String>>} weather
*/

const getWeatherInfo = (weatherCode) => {
  const pluginImagePaths = JWeatherCustomizerData.pluginImagePath;

  // 0 : Clear Sky
  if (weatherCode === 0) {
    return { label: "", icon: pluginImagePaths + '100.svg' };
  }
  if (weatherCode === 1) {
    return { label: "", icon: pluginImagePaths + '100.svg' };
  }
  // 2 : Partly Cloudy
  if (weatherCode === 2) {
    return { label: "", icon: pluginImagePaths + '101.svg' };
  }
  // 3 : Overcast
  if (weatherCode === 3) {
    return { label: "", icon: pluginImagePaths + '200.svg' };
  }
  // 45, 48 : Fog And Depositing Rime Fog
  if (weatherCode <= 49) {
    return { label: "", icon: pluginImagePaths + '200.svg' };
  }
  // 51, 53, 55 : Drizzle Light, Moderate And Dense Intensity  56, 57 : Freezing Drizzle Light And Dense Intensity
  if (weatherCode <= 59) {
    return { label: "", icon: pluginImagePaths + '202.svg' };
  }
  // 61, 63, 65 : Rain Slight, Moderate And Heavy Intensity 66, 67 : Freezing Rain Light And Heavy Intensity
  if (weatherCode <= 69) {
    return { label: "", icon: pluginImagePaths + '300.svg' };
  }
  // 71, 73, 75 : Snow Fall Slight, Moderate And Heavy Intensity  77 : Snow Grains
  if (weatherCode <= 79) {
    return { label: "", icon: pluginImagePaths + '400.svg' };
  }
  // 80, 81, 82 : Rain Showers Slight, Moderate And Violent
  if (weatherCode <= 84) {
    return { label: "", icon: pluginImagePaths + '302.svg' };
  }
  // 85, 86 : Snow Showers Slight And Heavy
  if (weatherCode <= 94) {
    return { label: "", icon: pluginImagePaths + '400.svg' };
  }
  // 95 : Thunderstorm Slight Or Moderate  96, 99 : Thunderstorm With Slight And Heavy Hail
  if (weatherCode <= 99) {
    return { label: "", icon: pluginImagePaths + '300.svg' };
  }
  // 
  return { label: "ERROR", icon: "" };
};

export default getWeatherInfo;
